.search {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    width: 90%;
    margin-top: 30px;
  
    gap: 20px;
  }
  
  .search-logo {
    height: 45px;
  }
  
  .search-body {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    max-width: 800px;
    width: 100%;
  
    gap: 10px;
  }
  
  .search-links {
    display: flex;
    gap: 8px;
  
  }
  
  .grey {
    color: grey;
  }
  
  .search-form {
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
  }
  
  .search-label {
    flex-grow: 1;
    position: relative;
  }
  
  .search-keyboard {
    position: absolute;
    top: 50%;
    right: 5px;
    transform: translate(0, -50%);
    height: 30px;
    object-fit: cover;
    opacity: 0.5;
  
    pointer-events: none;
  }
  
  .search-input {
    width: 100%;
    padding: 10px;
  
    outline: none;
    border: 2px solid #fff360;
  }
  
  .search-form button {
    position: relative;
    padding: 10px 20px;
    border: none;
    background-color: #fff360;
  
    cursor: pointer;
  }
  
  .search-form button::before {
    content: '';
    position: absolute;
    top: 0;
    right: -40px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 20px;
    border-color: transparent transparent transparent #fff360;
  }