.card2 {
    border-bottom: 1px solid transparent;
    cursor: pointer;
  }
  
  .card2, .news-currencies {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    gap: 10px;
  
    color: black;
  }
  
  .card-image {
    width: 100%;
    height: 180px;
  
    object-fit: cover;
  }
  
  .news-body .card-image {
    width: 20px;
    height: 20px;
  }
  
  .news-currencies {
    margin-top: 10px;
  }
  
  .card-title2 {
    color: #6fa3dd;
  }
  
  .card-active .card-title2 {
    color: #0b58b4;
    font-weight: 700;
  }
  
  .news-currencies .card-text-main {
    font-weight: 700;
  }
  
  .card-top-right {
    width: 220px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  
  .card-top-right .card-image {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
  }
  
  .card-top-right .card-title2 {
    color: #0b58b4;
    font-weight: 700;
  }
  
  .search-links .card-title2 {
    color: #0b58b4;
    font-weight: 500;
  }
  
  .banner {
    width: 90%;
  }
  
  .banner .card-image {
    width: 100%;
    height: 150px;
  }

  
  .banner .card2 {
    border: none;
  }
  
  .banner .card2:hover {
    border: none;
  }
  
  .footer-card > .card2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
    page-break-inside: avoid;
    break-inside: avoid-column;
    margin-top: 15px;
  }
  
  .footer-card .card-text-main, .footer-card .card-title2 {
    font-weight: 600;
  }
  
  .footer-card .card-text-secondery {
    color: grey;
  }
  
  .footer-card .card-title2 {
    color: #0b58b4;
    margin-bottom: 5px;
  }
  
  .card-weather .card-text-secondery {
    white-space: pre-wrap;
  }
  
  .card-weather .card-image {
    width: 35px;
    height: 35px;
  }
  